<template>
  <div class="listview">
      <div v-for="(item, index) in newsList"
          class="row" :class="{'grey-bg': index % 2 == 0}">
          <Item :news="item.title"></Item>
      </div>
  </div>
</template>
<script>
  import Item from '../Item/index.vue';
  import { mapActions,mapGetters } from 'vuex';
  export default {
    data(){
      return {
        type:'',
        start:'',
        len:20,
        http:'https://bxs.ghspace.cn/policy',
      }
    },
    components: {
      //Scroller,
      Item,
    },
    computed:{
      newsList(){
        return this.$store.getters.mypolicy;
      }
    },
    mounted() {
    },
    methods: {
      GetQueryString(name){
           var reg = new RegExp("(^|&)"+ name +"=([^&]*)(&|$)");
           var r = window.location.search.substr(1).match(reg);
           if(r!=null)return  unescape(r[2]); return null;
      },
      posthttp(type,start){
        let len=this.len;
        let http=`${this.http}/${type}/${len}/${start}`;
        console.log(start,type,len,http);
        this.$store.dispatch('getPolicy',{http:http,start:start,type:type});
      },
      //Y  
      getScrollTop(){
      var scrollTop = 0, bodyScrollTop = 0, documentScrollTop = 0;
      if(document.body){
      bodyScrollTop = document.body.scrollTop;
      }
      if(document.documentElement){
      documentScrollTop = document.documentElement.scrollTop;
      }
      scrollTop = (bodyScrollTop - documentScrollTop > 0) ? bodyScrollTop : documentScrollTop;
         console.log('Y',scrollTop);
      return scrollTop;
        }, 
       // 
       getScrollHeight(){
      var scrollHeight = 0, bodyScrollHeight = 0, documentScrollHeight = 0;
      if(document.body){
      bodyScrollHeight = document.body.scrollHeight;
      }
      if(document.documentElement){
      documentScrollHeight = document.documentElement.scrollHeight;
      }
      scrollHeight = (bodyScrollHeight - documentScrollHeight > 0) ? bodyScrollHeight : documentScrollHeight;
          console.log(':',scrollHeight);
      return scrollHeight;
        },
       // 
       getWindowHeight(){
      var windowHeight = 0;
      if(document.compatMode == "CSS1Compat"){
      windowHeight = document.documentElement.clientHeight;
      }else{
      windowHeight = document.body.clientHeight;
      }
          console.log('',windowHeight)
      return windowHeight;
       }
    },
    created:function(){
      console.log(this.$route.params.type);
      let type=this.GetQueryString('type');
      let start=this.GetQueryString('start');
      let vm=this;
      this.posthttp(type,start);
      window.onscroll = function(){
        setTimeout(function(){
            if((vm.getScrollTop() + vm.getWindowHeight())>vm.getScrollHeight()-20){
              console.log('bottom');
              vm.$store.dispatch('loaderMore',vm.http);
         }
        },300)
      };
    }
  }
</script>





