<template>
		<div id="app">
			<div class="header">
					<span @click="change('/policy',0,0)"></span>
				  <span @click="change('/money',1,0)"></span>
				  <span @click="change('/reduce',2,0)"></span>
				  <span @click="change('/qualification',3,0)"></span>
				  <span @click="change('/reader',4,0)"></span>
			</div>

		  <!--  -->
		  <!--  -->
		  <router-view></router-view>
		</div>
</template>
<script>
		export default{
			  name:'app',
	  		methods:{
	  			change(link,type,start){
	  				this.$router.push({ path:link, query: { type: type,start:start}})
	  			}
	  		}
		}
</script>
<style src="./assets/common.css"></style>


